body {
  width: 100%;
  max-width: 1200px;
  font-family: "Helvetica";
  background-color: rgb(5, 5, 5);
  color: #fff;
  margin: 0 auto;
}

.text-block {
  text-align: center;
}

h1 {
  font-size: 96px;
  padding: 30px 0px 30px 0px;
  font-weight: 900;
}

hr {
  margin: 0 auto;
  width: 1200px;
  margin-bottom: 30px;
  border: 1px solid #555;
}

.welcome {
  font-weight: 700;
  font-size: 28px;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.card {
  padding: 70px 5px;
  color: #000;
  background-color: #ddd;
  border-radius: 8px;
  margin: 50px auto;
}

.btn {
  margin-top: 30px;
  margin-left: 10px;
  padding: 15px 20px;
  background-color: #000;
  color: #fff;
  border-radius: 4px;
  float: left;
  font-size: 16px;
  position: relative;
}

.primary {
  margin-top: 30px;
  margin-left: 10px;
  padding: 15px 20px;
  background-color: #fff;
  color: #000;
  border-radius: 4px;
  float: left;
  font-size: 16px;
  position: relative;
}

.btn:hover {
  opacity: 0.9;
  transition: 0.2s ease-in-out;
}

.title {
  font-weight: 600;
  font-size: 24px;
  margin-left: 10px;
}

input {
  margin-top: 20px;
  margin-left: 10px;
  float: left;
}

input[type=file] {
  display: none;
}

.preview {
  display: flex;
  flex-wrap: wrap;
  padding-top: 20px;
}

.preview-image {
  margin-bottom: 10px;
  margin-left: 10px;
  position: relative;
  overflow: hidden;
}

.preview-image.removing {
  transform: scale(0);
  transition: transform 0.3s;
}

.preview-image:hover .preview-remove {
  opacity: 1;
}

.preview-image:hover .preview-info {
  bottom: 0px;
}

img {
  width: 150px;
  height: auto;
}

.preview-remove {
  opacity: 0;
  width: 20px;
  height: 20px;
  position: absolute;
  right: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: center;
  cursor: pointer;
  font-weight: 900;
  background-color: rgba(255, 255, 255, 0.8);
  transition: opacity 0.2s;
}

.preview-info {
  position: absolute;
  right: 0;
  bottom: -25px;
  left: 0;
  font-size: 10px;
  background-color: rgba(255, 255, 255, 0.5);
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 25px;
  padding: 8px 0px;
  transition: bottom 0.2s;
}/*# sourceMappingURL=style.css.map */